
.slider-thingy
  b-slider.slider(
    v-model="sliderValue"
    v-bind="sliderOptions"
    size="is-large"
    @dragging="dragging"
    @dragstart="dragStart"
    @dragend="dragEnd")

  .buttons
    .playpause(@click='toggleSimulation')
      i.button-icon.fa.fa-1x.fa-pause(v-if="isRunning")
      i.button-icon.fa.fa-1x.fa-play(v-else)

